import React, { Component } from 'react';
/*import Map from "./map";*/
import {Map, InfoWindow, Marker, GoogleApiWrapper} from 'google-maps-react';

export class MapContainer extends Component {
	googleMapsProps = {
		zoom: 14,
		scrollwheel: false,
		mapTypeControl: false,
		disableDefaultUI: true,
		zoomControl: true,
		mapTypeId: 'roadmap',
		scaleControl: false,
		streetViewControl: false,
		disableDoubleClickZoom: true,
		styles: [
			{
				stylers: [{ hue: "#00971f" }]
			}, {
				featureType: "road",
				elementType: "geometry",
				stylers: [
					{ lightness: 90 },
					{ visibility: "simplified" }
				]
			}
		],
		containerStyle: {
			width: '100%',
			height: '23rem'
		}
	};

	get map_center() {
		return this.props.locations[this.props.location_index];
	}

	render() {
		return (
			<Map google={this.props.google}
				 {...this.googleMapsProps}
				 initialCenter={this.map_center}
				 center={this.map_center}
			>
				{/*<InfoWindow >blah</InfoWindow>*/}
				<Marker
					title={'The marker`s title will appear as a tooltip.'}
					name={'test'}
					position={this.props.locations[0]}
				/>
				<Marker
					title={'The marker`s title will appear as a tooltip.'}
					name={'test'}
					position={this.props.locations[1]}
				/>
				<Marker
					title={'The marker`s title will appear as a tooltip.'}
					name={'test'}
					position={this.props.locations[2]}
				/>
			</Map>
		)
	}
}

export default GoogleApiWrapper({
	apiKey: 'AIzaSyB79kDJs_uDQBfAxyXPz96fWt5bk2fUAdc',
	version: '3.32.6'
})(MapContainer)


