import React, { Component, Fragment } from "react";
import isTouchDevice from 'is-touch-device';

import {
	Route,
	NavLink,
	BrowserRouter
} from "react-router-dom";

import Home from "./pages/home";
import Seed from "./pages/seed";
import About from "./pages/about";
import Information from "./pages/information";
import Team from "./pages/team";
import Partners from "./pages/partners";
import Contacts from "./pages/contacts";
import ScrollArea from "react-scrollbar";
import Footer from "./footer";
import { slide as Menu } from 'react-burger-menu'

function isTouch() {
	if (isTouchDevice()) {
		document.body.classList.add('is_touch');
	}
}

function ScrollOnDesktop({children}) {
	if (isTouchDevice()) {
		return (
			<Fragment>
				{children}
			</Fragment>
		);
	}

	const scroll_options = {
		speed: 1,
		className:"main-area",
		contentClassName:"scroll-area",
		vertical:true,
		horizontal:false,
		smoothScrolling:false,
		style: {
			height: '100%'
		}
	};

	return (
		<ScrollArea {...scroll_options}>
			{children}
		</ScrollArea>
	);
}

class Main extends Component {
	render() {
		isTouch();

		return (
			<BrowserRouter>
				<ScrollOnDesktop>
					<div className="main">
						<div className="top-bar">
							<div id="outer-container">
			  					<Menu right width={ '200px' } >
									<ul className="nav-mobile">
										<li className="menu-item"><NavLink onClick={() => this.closeMenu()} to="/" strict exact>Головна</NavLink></li>
										<li className="menu-item"><NavLink onClick={() => this.closeMenu()} to="/seed">Насіння</NavLink></li>
										<li className="menu-item"><NavLink onClick={() => this.closeMenu()} to="/about">Про нас</NavLink></li>
										<li className="menu-item"><NavLink onClick={() => this.closeMenu()} to="/team">Команда</NavLink></li>
										<li className="menu-item"><NavLink onClick={() => this.closeMenu()} to="/partners">Партнери</NavLink></li>
										<li className="menu-item"><NavLink onClick={() => this.closeMenu()} to="/contacts">Контакти</NavLink></li>
									</ul>
								</Menu>
							</div>
							<div className="content row">
								<NavLink to="/" strict exact>
									<span className="logo" />
								</NavLink>
								<ul className="menu">
									<li className="menu-item"><NavLink to="/" strict exact>Головна</NavLink></li>
									<li className="menu-item"><NavLink to="/seed">Насіння</NavLink></li>
									<li className="menu-item"><NavLink to="/about">Про нас</NavLink></li>
									<li className="menu-item hide"><NavLink to="/information">Демо Поля</NavLink></li>
									<li className="menu-item"><NavLink to="/team">Команда</NavLink></li>
									<li className="menu-item"><NavLink to="/partners">Партнери</NavLink></li>
									<li className="menu-item"><NavLink to="/contacts">Контакти</NavLink></li>
								</ul>
							</div>
						</div>

						<Route exact path="/" component={Home}/>
						<Route path="/seed" component={Seed}/>
						<Route path="/about" component={About}/>
						<Route path="/information" component={Information}/>
						<Route path="/team" component={Team}/>
						<Route path="/partners" component={Partners}/>
						<Route path="/contacts" component={Contacts}/>
					</div>
					<Footer />
				</ScrollOnDesktop>
			</BrowserRouter>
		);
	}
}

export default Main;


