import React, { Component } from "react";
import {
	NavLink
} from "react-router-dom";
import PartnersTypes from './partners_types.json';

class PartnersSubmenu extends Component {
	items = PartnersTypes.map(partnerTypeItem => (
		<li className="submenu-item" key={`submenu_item_${partnerTypeItem.id}`}>
			<NavLink replace to={`/partners/${partnerTypeItem.path}`}>
				{partnerTypeItem.title}
			</NavLink>
		</li>
	));

	render() {
		return (
			<div className="content _full _no-padding">
				<ul className="submenu">
					{this.items}
				</ul>
			</div>
		);
	}
}

export default PartnersSubmenu;


