import React, { Component } from "react";
import SeedsPropTypes from './props_types.json';
import PropsTitles from './props.json';

const getPropTypeTitleById = (id) => (
	SeedsPropTypes.find(prop => prop.id === id).title
);

class Qualities extends Component {
	render() {
		return this.props.props.map((seeds_prop_type) => {
			const items = seeds_prop_type.items.map((seed_prop) => {
				const title = PropsTitles.find((prop_title_item) => prop_title_item.id === seed_prop.id).title;
				const value = seed_prop.value;

				if (seed_prop.id === 17) {
					return (
						<div className="specs" key={`seed_prop_${seed_prop.id}`}>
							<div className="spec-title">{value}</div>
							<div className="spec-value">&nbsp;</div>
						</div>
					);
				}

				if (seed_prop.id === 22) {
					return (
						<div className="specs" key={`seed_prop_${seed_prop.id}`} />
					);
				}

				return (
					<div className="specs" key={`seed_prop_${seed_prop.id}`}>
						<div className="spec-title">{title}:</div>
						<div className="spec-value">{value}</div>
					</div>
				)
			});

			const prop_type_title = getPropTypeTitleById(seeds_prop_type.id);

			return (
				<div className={`seed-table seed-prop-${seeds_prop_type.id}`} key={`seed_prop_type_${seeds_prop_type.id}`}>
					<div className="title">{prop_type_title}</div>
					<div className="row">{items}</div>
				</div>
			)
		})
	}
}

export default Qualities;


