import React, { Component } from 'react';

import {
	Route,
	Switch
} from "react-router-dom";

import SeedHome from "./seed/seed_home";
import Submenu from "./seed/submenu"

import Products from "./seed/products";
import SeedTypesList from "./seed/seeds_types.json";
import SeedsList from "./seed/products.json";

export default class Seed extends Component {
	get routes() {
		return SeedTypesList.map(seed_type => {
			const seed_items = seed_type.items.map((seed_id) => {
				const isIdMatch = (seed_item) => seed_item.id === seed_id;
				return SeedsList.find(isIdMatch);
			});

			const render = (props) => (
				<Products {...props}
						  {...seed_type}
						  items={seed_items}
				/>
			);

			return <Route path={`/seed/${seed_type.path}`}
				   render={render}
				   key={`${seed_type.id}_route`}
			/>
		});
	}

	render() {
		return (
			<Switch>
				<div className="page-seeds">
					<Submenu />
					<Route exact path="/seed" component={SeedHome}/>
					{this.routes}
				</div>
			</Switch>
		);
	}
};


