import React, { Component, Fragment } from "react";
import Qualities from "./qualities";
import ScrollArea from "react-scrollbar";
import isTouchDevice from 'is-touch-device';

function ScrollOnDesktop({children}) {
	if (isTouchDevice()) {
		return (
			<Fragment>
				<div className="popup-area">
					<div className="popup-scroll-area">
						{children}
					</div>
				</div>
			</Fragment>
		);
	}

	const scroll_options = {
		speed: 1,
		className: "popup-area",
		contentClassName: "popup-scroll-area",
		vertical: true,
		horizontal: false,
		smoothScrolling: false,
		style: {
			height: '100%'
		}
	};

	return (
		<ScrollArea {...scroll_options}>
			{children}
		</ScrollArea>
	);
}

class Details extends Component {
	get details() {
		return (
			<ul>
				{this.props.details_list.map((item, index) => (
					<li key={`details_item_${index}`}>{item}</li>
				))}
			</ul>
		);
	}

	render() {
		const {title, subtitle} = this.props;

		return (
			<div className="seed-details">
				<div className="seed-title">
					&laquo;{title}&raquo;
					<div className="seed-subtitle">{subtitle}</div>
				</div>
				<ScrollOnDesktop>
					<div className="seed-qualities">
						{this.details}
					</div>
					<Qualities {...this.props}/>
				</ScrollOnDesktop>
			</div>
		);
	}
}

export default Details;


