import React, { Component } from "react";
import SkyLight from 'react-skylight';
import Details from './details';

class Product extends Component {
	get new_label() {
		if (this.props.new === true) {
			return (
				<div className="seed-new" />
			);
		}
	}

	_executeBeforeModalOpen(){
		document.body.classList.add('modal-open');
	}

	_executeAfterModalClose(){
		document.body.classList.remove('modal-open');
	}

	render() {
		let product_dialog_styles = {
			dialogStyles: {
				padding: '2rem',
				width: 'auto',
				top: '10%',
				left: '10%',
				right: '10%',
				bottom: '10%',
				marginTop: '0',
				marginLeft: '0'
			},
			closeButtonStyle: {
				cursor: 'pointer',
				position: 'absolute',
				fontSize: '3rem',
				lineHeight: '1.7rem',
				fontWeight: '100',
				right: '10px',
				top: '10px',
				color: '#004E27',
				overflow: 'hidden',
				zIndex: '100'
			}
		};

		return (
			<div className="product-item-container">
				<button className="product-item-mask" onClick={() => this.productDetails.show()}>
					<div className="product-item">
						<img src={require(`../../images/seeds/${this.props.img}.jpg`)} alt="" />
					</div>
					<div className="seed-name">{this.props.title}</div>
					<div className="seed-type">
						<div className="seed-type-caption">
							{this.props.title}
						</div>
					</div>
					<div className="seed-logo" />
					{this.new_label}
				</button>
				<SkyLight
					dialogStyles={product_dialog_styles.dialogStyles}
					closeButtonStyle={product_dialog_styles.closeButtonStyle}
					hideOnOverlayClicked
					ref={ref => this.productDetails = ref}
					beforeOpen={this._executeBeforeModalOpen}
					afterClose={this._executeAfterModalClose}
				>
					<div className="product-popup">
						<div className="product-title">
							<h3>{this.props.title}</h3>
						</div>
						<div className="product-item">
							<div className="product-image">
								<img src={require(`../../images/seeds/${this.props.img}.jpg`)} alt="" />
							</div>
							<Details {...this.props} />
						</div>
					</div>
				</SkyLight>
			</div>
		);
	}
}

export default Product;


