import React, { Component } from 'react';

import {
	Route,
	Switch,
	Redirect
} from "react-router-dom";

import PartnersSubmenu from "./partners/submenu";


import Partners from "./partners/partners";
import PartnersTypes from "./partners/partners_types.json";
import PartnerList from "./partners/partners.json";

export default class PartnersPage extends Component {
	get routes() {
		return PartnersTypes.map(partner_type => {
			const partner_items = partner_type.items.map((partner_id) => {
				const isIdMatch = (partner_item) => partner_item.id === partner_id;
				return PartnerList.find(isIdMatch);
			});

			const render = (props) => (
				<Partners {...props}
						  {...partner_type}
						  items={partner_items}
				/>
			);

			return <Route path={`/partners/${partner_type.path}`}
						  render={render}
						  key={`${partner_type.id}_route`}
			/>
		});
	}

	get rootRoute() {
		const render = () => (
			<Redirect to="/partners/distributors"/>
		);

		return <Route exact path={`/partners/`}
					  render={render}
		/>
	}

	render() {
		return (
			<Switch>
				<div className="page-partners">
					<PartnersSubmenu />
					{this.routes}
					{this.rootRoute}
				</div>
			</Switch>
		);
	}
};


