import React, { Component } from 'react';

export default class SeedSwiper extends Component {

	render() {
		return (
			<a href={`/seed/${this.props.path}`} className="cat-item swiper-slide">
				<span className={`cat-background ${this.props.class}`} />
				<span className="caption">{this.props.title}</span>
			</a>
		);
	}
};


