import React, { Component } from 'react';
import SeedSwiper from "./seed_swiper";
import Swiper from 'react-id-swiper';


export default class Home extends Component {

	get slides() {

		const swiper_slide = [
			{ path: "wheat/winter", 	class: "_wheat", 		title: "Пшениця" },
			{ path: "soy", 				class: "_soy", 			title: "Соя" },
			{ path: "raps", 			class: "_raps", 		title: "Ріпак" },
			{ path: "barley/spring", 	class: "_barley", 		title: "Ячмінь" },
			{ path: "sunflower", 		class: "_sunflower", 	title: "Соняшник" }
		];

		return swiper_slide.map(slide =>
			<SeedSwiper {...slide} />
		)
	}

	render() {

		const swiper_setup = {
			slidesPerView: 5,
			breakpoints: {
				1024: {
					slidesPerView: 4
				},
				768: {
					slidesPerView: 3
				},
				640: {
					slidesPerView: 2
				},
				320: {
					slidesPerView: 1
				}
			}
		};

		return (
			<div className="homepage">
				<header className="header">
					<div className="header-background">
						<div className="header-image">
							<div className="header-quote">
								<div className="quote-part">– Wie du säst, so wirst du ernten!</div>
								<div className="quote-part">– Що посієш – те й пожнеш!</div>
							</div>
							<a href="http://www.probstdorfer.at/" target="_blank">
								<span className="logo-probs" />
							</a>
						</div>
					</div>
				</header>
				<div className="categories">
					<Swiper {...swiper_setup}>
						{this.slides}
					</Swiper>
				</div>
				<div className="content we-are">
					<div className="columns">
						<div className="left-column">
							<h2 className="title">Хто Ми Такі</h2>
							<div className="description">На сьогоднішній день компанія володіє виробничими потужностями як в Австрії, так і в Угорщині та Румунії. Професійний супровід на всіх етапах насінництва, сучасне обладнання з високою виробничою потужністю, власна акредитована лабораторія з оцінки якості насіння – все це забезпечує репутацію надійності, стабільності та гарантії якості серед наших клієнтів та партнерів на європейському ринку насіння.</div>
							<div className="description"><span className="_bold">Probstdorfer Saatzucht</span> – це вже понад 70 років успішної історії, наполегливої праці, традицій, інновацій та найкращого насіння.</div>
						</div>
						<div className="right-column">
							<div className="we-are-image" />
						</div>
						<div className="achievements">
							<div className="achievement">
								<div className="top">30</div>
								<div className="bottom">проектів</div>
							</div>
							<div className="achievement">
								<div className="top">360</div>
								<div className="bottom">клієнтів</div>
							</div>
							<div className="achievement">
								<div className="top">5</div>
								<div className="bottom">офісів</div>
							</div>
							<div className="achievement">
								<div className="top">35</div>
								<div className="bottom">нагород</div>
							</div>
						</div>
					</div>
				</div>
			</div>
		);
	}
};


