import React, { Component } from "react";
import {
	NavLink
} from "react-router-dom";
import SeedsTypes from './seeds_types.json';

class Submenu extends Component {
	items = SeedsTypes.map(seedTypeItem => (
		<li className="submenu-item" key={`submenu_item_${seedTypeItem.id}`}>
			<NavLink replace to={`/seed/${seedTypeItem.path}`}>
				{seedTypeItem.title}
			</NavLink>
		</li>
	));

	render() {
		return (
			<div className="content _full _no-padding">
				<ul className="submenu">
					{this.items}
				</ul>
			</div>
		);
	}
}

export default Submenu;


