import React, { Component } from 'react';

import Member from "./team/member";
import MembersJson from "./team/members.json";

export default class Team extends Component {
	members = MembersJson.map(teamMember => (
		<Member key={`view_option_${teamMember.id}`} {...teamMember} />
	));

	render() {
		return (
			<div className="page-team">
				<div className="content">
					<div className="team-title">
						<h2>Наша команда</h2>
						<div className="team-description">Lorem ipsum dolor sit amet, consectetur adipisicing elit. Esse est distinctio excepturi consequuntur aperiam ipsa similique ab doloribus aut aliquid, necessitatibus eveniet, voluptatem exercitationem! Consectetur animi voluptatem optio delectus, illo?
						</div>
					</div>
					<div className="team">
						{this.members}
					</div>
				</div>
			</div>
		);
	}
};


