import React, { Component } from "react";

class Partner extends Component {
	get details() {
		return (
			<div>
				{this.props.phone_numbers.map((item, index) => (
					<a className="phone link" key={`details_item_${index}`} href={`tel:${item}`}>{item}</a>
				))}
			</div>
		);
	}
	render() {
		return (
			<div className="partner-item-container">
				<div className="partner-item-mask">
					<div className="partner-img"><img src={require(`../../images/partners/${this.props.img}`)} alt="" /></div>
					<div className="caption">{this.props.title}</div>
					<div className="partner-item">
						<div className="partner-details">
							<a href={`http://${this.props.website}/`} target="_blank" className="website">{this.props.website}</a>
							{this.details}
							<a className="email link" href={`mailto:${this.props.email}`}>{this.props.email}</a>
						</div>
					</div>
				</div>
			</div>
		);
	}
}

export default Partner;


