import React, { Component } from "react";

class Member extends Component {
	get tel() {
		if (this.props.hasOwnProperty('tel')) {
			return (
				<p>Тел/Факс: <a className="team-call link" href={`tel:${this.props.tel}`}>{this.props.tel}</a></p>
			);
		}
	}
	get mobile() {
		if (this.props.hasOwnProperty('mobile')) {
			return (
				<p>Тел: <a className="team-call link" href={`tel:${this.props.mobile}`}>{this.props.mobile}</a></p>
			);
		}
	}
	render() {
		return (
			<div className="member-item-container">
				<div className="member-item-mask">
					<div className="member-item">
						<img src={require(`../../images/team/thumbs/${this.props.img}`)} alt="" />
						<div className="member-item-title">
							<div className="member-name-mobile">{this.props.name}</div>
							<p className="job-mobile">{this.props.title}</p>
						</div>
					</div>
					<div className="caption-team">
						<div className="member-name">{this.props.name}</div>
						<p className="job">{this.props.title}</p>
						{this.tel}
						{this.mobile}
						<a className="team-mail link" href={`mailto:${this.props.email}`}>{this.props.email}</a>
					</div>
				</div>
			</div>
		);
	}
}

export default Member;


